import React, { Component } from 'react';
import PropTypes from 'prop-types';
import PostImg from './PostImg';

let lastScrollY = 0;
let ticking = false;

// Slightly modified version to quickly return a string
// https://stackoverflow.com/a/5624139/1218980
function hexToRgb(hex) {
    // Expand shorthand form (e.g. "03F") to full form (e.g. "0033FF")
    const shorthandRegex = /^#?([a-f\d])([a-f\d])([a-f\d])$/i;
    hex = hex.replace(shorthandRegex, function(m, r, g, b) {
        return r + r + g + g + b + b;
    });

    const result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
    return result ? "rgb(" + [
        parseInt(result[1], 16),
        parseInt(result[2], 16),
        parseInt(result[3], 16)
    ].join(', ') + ")" : null;
}

export default class PostImgColored extends Component {
    constructor(props) {
        super(props);
        this.ref = React.createRef();

        this.startPercentage = 0.6;
        this.endPercentage = 0.2;
    }

    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    }

    handleScroll = () => {
      lastScrollY = window.scrollY;

        if (!ticking) {
            window.requestAnimationFrame(() => {
                console.log('hello');
                console.log(this.getPercentageHeight());
                ticking = false;
            });

            ticking = true;
        }
    };

    getPercentageHeight = () => {
        const viewport = Math.max(document.documentElement.clientHeight, window.innerHeight || 0);

        const elementOffset = this.ref.current.getBoundingClientRect().top;

        return elementOffset/viewport;
    };

    render() {
        const { props } = this;
        const { src } = props;

        return (
            <div ref={this.ref}>
                <PostImg src={src} {...props} />
            </div>
        );
    }
}

PostImgColored.propTypes = {
    src: PropTypes.string.isRequired,
    bgColor: PropTypes.string.isRequired,
};

